<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="5">
        <el-input v-model="keywords" @keyup.enter.native="search" placeholder="请输入用户名称/手机号"></el-input>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-col>
      <el-col :span="5" :offset="12">
        <el-button type="primary" @click="new_user">新建用户</el-button>
      </el-col>
    </el-row>
    <el-row class="mtb10">
      <el-table
        :data="tableData"
        class="w24"
        @filter-change="binFilters"
        border
        height="500px"
        :default-sort="{prop: 'userName', order: 'descending'}"
        :row-class-name="tableRowClassName"
        ref="filterTable"
      >
        <el-table-column prop="userName" label="用户名称" sortable></el-table-column>
        <el-table-column prop="userId" label="手机号码"></el-table-column>
        <el-table-column prop="roles" label="角色" :filters="filtersRoles" column-key="roles"></el-table-column>
        <el-table-column
          prop="faculties"
          label="院系"
          :filters="filtersFaculties"
          column-key="faculties"
        ></el-table-column>
        <el-table-column prop="clazzs" label="班级"></el-table-column>
        <el-table-column
          prop="status_text"
          label="状态"
          :filters="[{ text: '可用', value: '1' },
        { text: '停用', value: '2' }]"
          column-key="status"
          :filter-multiple="false"
        ></el-table-column>
        <el-table-column label="操作" width="300">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="change_info_user(scope.row)">编辑</el-button>
            <el-button size="mini" type="primary" @click="orle_user(scope.$index, scope.row)">角色分配</el-button>
            <el-button
              size="mini"
              :type="scope.row.status == 1?'primary':'danger'"
              @click="handleDelete( scope.row,scope.$index,)"
            >功能{{scope.row.status == 1?'停用':'可用'}}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row class="mt10 df jcfe">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="current_page"
        :page-sizes="[5,50, 100, 200]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
// import pagination from "@/components/pagination.vue";
export default {
  // components: { pagination },
  data() {
    return {
      tableData: [], //表数据
      filtersRoles: [], //可以筛选的所有角色数组
      filtersFaculties: [], //可以筛选的所有院系数组
      filters: {
        filtersRole: [],
        filtersStatus: []
      }, //已经选中的筛选数组
      keywords: "", //搜索输入
      total: 0, //总共有多少条数据
      current_page: 1, //当前选择的第几页
      page_size: 5 //当前一页选择的条数
    };
  },
  mounted() {
    //this.getUsersList()
    this.$post("user/getgroupid").then(res => {
      res.roles.forEach(item => {
        item.text = item.role_name;
        item.value = item.role_id;
      });
      res.faculties.forEach(item => {
        item.text = item.faculty_name;
        item.value = item.faculty_id;
      });
      this.filtersRoles = res.roles;
      this.filtersFaculties = res.faculties;
    });
  },
  methods: {
    //触发筛选
    binFilters(res) {
      console.log(res);
      if ("roles" in res) this.filters.filtersRole = res.roles;
      if ("faculties" in res) this.filters.filtersStatus = res.faculties;
      //this.getUsersList();
    },
    //新建用户
    new_user() {
      this.$router.push({
        path: "/page/user/userNew",
        query: {}
      });
    },
    //修改用户信息
    change_info_user(res) {
      this.$router.push({
        path: "/page/user/userInfoChange",
        query: {}
      });
    },
    //点击角色分配的时候触发
    orle_user() {
      this.$router.push("/page/user/userRole");
    },
    //点击搜索时候触发
    search() {
      this.$get("users/keywords", {
        keywords: this.keywords,
        current_page: this.current_page,
        page_size: this.page_size
      }).then(res => {
        this.$refs.filterTable.clearFilter();
        this.filters.filtersRole = [];
        this.filters.filtersStatus = [];
        this.filters.filtersOffice = [];
        this.tableData = res.data;
        this.total = res.total;
      });
    },
    //点击功能禁用和启用触发
    handleDelete(row, index) {
      this.$confirm(
        "确定" + (row.status == "1" ? "停用" : "启用") + "用户?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).then(() => {
        this.$put(
          row.status == "1" ? "users/freezeUser" : "users/unFreezeUser",
          { id: row.id }
        ).then(res => {
          this.getUsersList();
        });
      });
    },
    //遍历过滤数据
    tableRowClassName({ row, rowIndex }) {
      let role = [];
      row.userRoles.forEach(e => {
        role.push(e.roleName);
      });
      row.role = role.join(",");
      if (row.status == "1") {
        row.status_text = "可用";
      } else if (row.status == "2") {
        row.status_text = "停用";
      }
      if (row.status == "2") {
        return "warning-row";
      }
      return "";
    },
    //该表一页的大小
    handleSizeChange(res) {
      this.page_size = res;
      this.getUsersList();
    },
    //换页
    handleCurrentChange(res) {
      this.current_page = res;
      this.getUsersList();
    },
    //筛选列表请求或者普通列表请求
    getUsersList() {
      if (
        this.filters.filtersRole.length ||
        this.filters.filtersOffice.length ||
        this.filters.filtersStatus.length
      ) {
        this.$get("users/filter", {
          roleId: this.filters.filtersRole.join(","),
          status: this.filters.filtersStatus[0],
          tenantId: this.filters.filtersOffice.join(","),
          current_page: 1,
          page_size: this.page_size
        }).then(res => {
          this.tableData = res.data;
          this.total = res.total;
          this.keywords = "";
        });
      } else {
        this.$get("users", {
          current_page: this.current_page,
          page_size: this.page_size
        }).then(res => {
          this.tableData = res.data;
          this.total = res.total;
        });
      }
    }
  }
};
</script>

<style>
.el-table .warning-row {
  background: oldlace !important;
}
</style>


